import { Heading1Data, SubjectProperties } from '@ali/4ever-mo';
import { Block, BlockProperties } from '@ali/4ever-cangjie';

/**
 * 设置议题完成状态
 */
export const TOGGLE_FINISHED = Symbol('TOGGLE_FINISHED');

export interface ToggleFinishedPayload {
  node: Block<Heading1Data>;
  finished: boolean;
}

export function toggleFinished(payload: ToggleFinishedPayload) {
  return {
    type: TOGGLE_FINISHED,
    payload,
  };
}

/**
 * 插入议题
 */
export const INSERT_SUBJECT = Symbol('INSERT_SUBJECT');

export interface InsertSubjectPayload {
  /**
   * 在节点的位置插入
   */
  node?: Block<Heading1Data>;
  /**
   * 在节点前/后插入
   * @default after
   */
  direction?: 'before' | 'after';
  /**
   * 议题属性
   */
  properties: SubjectProperties;
}

export function insertSubject(payload: InsertSubjectPayload) {
  return {
    type: INSERT_SUBJECT,
    payload,
  };
}

/**
 * 插入空段落
 */
export const INSERT_BLOCK = Symbol('INSERT_BLOCK');

export interface InsertBlockPayload {
  blockProps: string | Block | BlockProperties;
}

export function insertBlock(payload: InsertBlockPayload) {
  return {
    type: INSERT_BLOCK,
    payload,
  };
}

/**
 * 高亮议题区块
 */
export const HIGHLIGHT_SUBJECT = Symbol('HIGHLIGHT_SUBJECT');

export interface HighlightSubjectPayload {
  subjectId: string;
  highlight: boolean;
}

export function highlightSubject(payload: HighlightSubjectPayload) {
  return {
    type: HIGHLIGHT_SUBJECT,
    payload,
  };
}

